import {Pipe,PipeTransform } from '@angular/core';

@Pipe({
    'name':'priceFormat'
})
export class priceFormatPipe implements PipeTransform{

    transform(input:any){
        console.log(typeof input);
        let index = input.toString(10).indexOf('.');
        return input.toString(10).slice(0,index+3);
    }

}